<template>
  <div class="doctorCollate">
    <!-- 导航 -->
    <Breadcrumbs :items="items" />
    <v-alert
      class="null__"
      :value="true"
      v-if="desserts.length == 0"
      color="success"
      icon="check_circle"
      outline
    >已全部审核完成，暂时没有新的医生提交认证。</v-alert>
    <!-- 数据表格 -->
    <v-container grid-list-md text-xs-center v-else>
      <v-layout row wrap>
        <v-flex xs12 sm12 v-for="(item,index) in desserts" :key="index">
          <v-card>
            <v-img :src="item.certified_files" aspect-ratio="5.75" @click="img_( item.certified_files )" ></v-img>
            <v-card-title primary-title>
              <div>
                <h3 class="headline mb-0">{{ item.name }}</h3>
                <div>
                  <v-list-tile>
                    <v-list-tile-action>
                      <v-icon color="indigo">phone_iphone</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>手机号码</v-list-tile-title>
                      <v-list-tile-sub-title>
                        <v-chip class="grey white--text darken-2">{{ item.phone | cover }}</v-chip>
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-divider inset></v-divider>
                  <v-list-tile>
                    <v-list-tile-action>
                      <v-icon color="indigo">perm_identity</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>医生职称</v-list-tile-title>
                      <v-list-tile-sub-title>
                        <v-chip class="grey white--text darken-2" v-if="item.level == 1" >主任医师</v-chip>
                        <v-chip class="grey white--text darken-2" v-if="item.level == 2" >副主任医师</v-chip>
                        <v-chip class="grey white--text darken-2" v-if="item.level == 3" >主治医师</v-chip>
                        <v-chip class="grey white--text darken-2" v-if="item.level == 4" >住院医师</v-chip>
                        <v-chip class="grey white--text darken-2" v-if="item.level == 5" >助理医师</v-chip>
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-divider inset></v-divider>
                  <v-list-tile>
                    <v-list-tile-action>
                      <v-icon color="indigo">timer</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>创建时间</v-list-tile-title>
                      <v-list-tile-sub-title>
                        <v-chip class="grey white--text darken-2">{{ item.created_time | time }}</v-chip>
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-divider inset></v-divider>
                </div>
              </div>
            </v-card-title>
            <v-card-actions>
              <v-btn flat color="orange">稍后</v-btn>
              <v-btn color="warning" @click="doctor_check( item )">立即审核</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>

    <!-- 确认框 -->
    <!-- <v-dialog v-model="dialog_logout" max-width="290">
        <v-card>
          <v-card-title class="headline">删除操作</v-card-title>
          <v-card-text> 确认删除用户吗？确认删除将不可恢复 </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat="flat" @click="dialog_logout = false">取消</v-btn>
            <v-btn color="green darken-1" flat="flat" @click="">确认删除</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>-->

  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";

export default {
  components: {
    Breadcrumbs
  },
  data() {
    return {
      // 确认框
      //   dialog_logout: false,
      user_id: "",
      loader: false,
      page: 0,
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "待审核医生列表",
          disabled: false,
          to: "/doctorCollate"
        }
      ],
      desserts: []
    };
  },
  mounted: function() {
    this.info();
  },
  methods: {
    /** 图片预览 */
    img_( img ){
      window.console.log( document.body.clientHeight )
      window.open(img,'newwindow', `height=600, width=1000, top=${ 150 }, left=${ document.body.clientWidth/2 - 500 }, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=no, status=no`);
    },
    doctor_check(data) {
      this.$router.push({
        path: `/doctorInfo`,
        query: {
          data: data
        }
      });
    },
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    info() {
      this.loader = true;
      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_list,
        data: {
          status: 2,
          page: 0,
          page_count: 100
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            if (response.data.datas.list) {
              this.desserts = response.data.datas.list;
            }
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
  padding-right: 10px;
  padding-left: 10px;
}
.v-card__title--primary > div {
  width: 100%;
}


.null__ {
  margin-top: 50px;
}
</style>
